  text-align: right;
}

.categories-list-element:not(:first-child) {
  padding-top: 9px;
}

.categories-list-element:not(:last-child) {
  padding-bottom: 9px;
}

.categories-list-element:hover {
  color: #9bb537;
  outline-color: rgba(0, 0, 0, 0);
}

.categories-list-element.is-active {
  color: #9bb537;
}

.categories-list::-webkit-scrollbar {
  width: 8px;
}

.categories-list::-webkit-scrollbar-track {
  background: none;
}

.categories-list::-webkit-scrollbar-thumb {
  height: 116px;
  background-color: rgba(5, 5, 5, .18);
  border-radius: 12px;
}

