.backdrop-order {

    width: 100%;
    height: 100%;
    background: rgba(46, 47, 66, 0.4);

    position: fixed;
    top: 0;
    left: 0;
    transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
        visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
    opacity: 0;
    visibility: hidden;
}

.backdrop-order.active {
    opacity: 1;
    visibility: visible;
}

@media screen and (max-width: 767px) {

    .modal-order {
        width: 95%;
        max-width: 335px;
        background-color: var(--primary-white-color);
        padding: 28px 20px;
        border-radius: 15px;

        /* position: relative; */
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        opacity: 0;
        visibility: hidden;
    }

    .modal-order.active {
        opacity: 1;
        visibility: visible;
    }

    .label-text {
        color: rgba(5, 5, 5, 0.50);
        font-size: 14px;

        font-weight: 500;
        line-height: 1.3;


    }

    .name-modal {
        color: #050505;

        font-size: 18px;

        font-weight: 600;
        line-height: 1.2;
        text-transform: uppercase;
        margin-bottom: 28px;


    }

    .close-modal-order {

        width: 20px;
        height: 20px;
        position: absolute;
        top: 14px;
        right: 14px;
        background-color: transparent;
        border-color: transparent;
        cursor: pointer;
    }

    .label-form {
        display: flex;
        gap: 8px;
        flex-direction: column;
    }

    .label-form:not(:last-child) {
        margin-bottom: 16px;
    }

    .order-input {

        padding: 14px 18px 14px 18px;

        border-radius: 15px;
        border: 1px solid rgba(5, 5, 5, 0.20);
    }

    .order-textarea {


        border-radius: 15px;
        border: 1px solid rgba(5, 5, 5, 0.20);
        padding: 14px 18px 14px 18px;
        resize: none;

    }

    .label-textarea.label-form {
        margin-bottom: 40px;
    }

    .btn-submit {
        display: flex;
        width: 295px;
        height: 46px;
        justify-content: center;
        align-items: center;
        margin-left: auto;
        margin-right: auto;
        border: none;
        border-radius: 15px;
        background: #9BB537;

        color: #F8F8F8;

        font-size: 14px;
        font-family: Inter;
        font-weight: 500;
        line-height: normal;
        cursor: pointer;
    }

}

